
import { Options, Vue, setup } from "vue-class-component";
import { formatTime } from "@/utils";
import { MSG_TYPE } from "@/const";
import { Icon, Tag } from "vant";
import { ref } from "vue";

class Props {
  msg!: Record<string, any>;
  timestamp!: number;
}

@Options({
  props: {
    msg: {
      default: () => {
        return {};
      }
    },
    timestamp: 0
  },
  components: {
    Icon,
    Tag
  },
  emits: ["previewImg", "previewCombineMsg"]
})
export default class MessageLeft extends Vue.with(Props) {
  leftMsg = setup(() => {
    const times = ref(0); // 加载视频重试次数

    const previewImg = (url: string) => {
      this.$emit("previewImg", url);
    };
    const downloadAttach = (url: string) => {
      window.open(url);
    };

    const onVideoError = (id: string, src: string) => {
      let timerId: number = 0;
      timerId && clearInterval(timerId);
      // 加载失败进行重试
      if (times.value < 5) {
        times.value++;
        timerId = setTimeout(() => {
          let video = document.getElementById(id) as HTMLVideoElement;
          video.setAttribute("src", src);
        }, 1000);
      }
    };

    const getCombineMsg = (msg: any) => {
      this.$emit("previewCombineMsg", msg);
    };

    return {
      formatTime,
      msgType: MSG_TYPE,
      getCombineMsg,
      previewImg,
      downloadAttach,
      onVideoError
    };
  });
}
